p {
  margin: 0px;
  font-size: 1.2em;
  color: whitesmoke;
  font-weight: bold;
}

.nav-bar--div {
  position: relative;
  grid-row: 1 / span 2;
  grid-column: 1 / span 20;
  height: 75%;
  align-items: center;
  backdrop-filter: blur(2px);
}

.nav-bar--nav {
  position: relative;
  display: flex;
  background-color: rgba(255, 255, 255, 0.124);

  height: 100%;

  vertical-align: middle;
  box-shadow: 0px 2px 4px 3px rgba(64, 64, 65, 0.2);
}

.nav-bar--nav div {
  position: relative;
  display: block;
  margin-top: auto;
  margin-bottom: auto;
  white-space: nowrap;

  text-align: center;
  user-select: none;

  padding: 0px 30px;
  padding: 0px 2%;
}

.nav-link--div {
  cursor: pointer;
  padding: 0px 4%;
}

.nav-link--div div:hover {
  font-style: italic;
}
