.modal--master-div {
  position: relative;
  grid-row: 5;
  grid-column: 11 / span 8;
  width: 100%;
  overflow: hidden;
  border: rgb(216, 209, 209) solid 4px;
  border-radius: 20px;
  height: fit-content;
  box-shadow: 7px 12px 16px 4px rgba(16, 15, 15, 0.254);
  background-image: linear-gradient(
    to bottom right,
    rgba(223, 223, 223, 0.2),
    rgba(243, 234, 234, 0.2)
  );
  /* background-color: whitesmoke; */
}

.modal--body-div {
  position: relative;
  padding: 30px 20px 40px 20px;
  text-align: center;
}

.modal--body-div p {
  margin: 0px;
  padding: 0px;
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
  user-select: none;
}

.modal--body-div p {
  margin-bottom: 15px !important;
}

.modal--body-div p:last-child {
  margin-bottom: 0px !important;
}
