.App {
  position: absolute;
  top: 0px;
  left: 0px;
  height: fit-content;
  width: 100%;
  min-width: 900px;

  /* overflow-x: hidden; */
}

hr {
  background-color: red;
  color: red;
  opacity: 25%;
}

.section-hub--landing {
  position: relative;
  left: 0px;
  top: 0px;
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(20, 5%);
  grid-template-rows: repeat(20, 5%);
  background-image: linear-gradient(
    to bottom right,
    rgba(252, 38, 0, 0.693),
    rgba(255, 0, 0, 0.7)
  );
  /* overflow-x: hidden; */
}

.section-hub--details {
  position: relative;
  height: fit-content;
  width: 100%;
  padding-bottom: 50px;
}

.section-hub--details p {
  color: rgb(84, 34, 34);
}

.section-hub--details-container--div {
  position: relative;
  width: 48.5%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.details--header--div {
  position: relative;
  width: 100%;
  /* background-color: aquamarine; */
}

.details--header--div p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2em;
  line-height: 1.5em;
}

.details-sectional--div {
  position: relative;
  min-height: 150px;
  margin-top: 60px;
  /* background-color: red; */
}

.sectional-header--div {
  position: relative;

  /* background-color: red; */
}

.sectional-header--div p {
  font-size: 1.5em;
}

.horizonral-divider {
  width: 25px;
  height: 4px;
  margin-top: 10px;
  background-color: rgba(216, 60, 60, 0.522);
}

.sectional-content--div {
  display: grid;

  padding-top: 25px;

  grid-template-columns: 100% 0%;
  grid-template-rows: 100%;
}

.sectional-content-text-div {
  /* border: red solid 1px; */
  text-align: left;
  font-weight: 400;
}

.sectional-content-text-div p {
  font-size: 1.1em;
  text-align: left;
  line-height: 30px;
  font-weight: 400;
}

.section-hub--foooter {
  width: 100%;
  position: relative;
  height: fit-content;
}

.hub-footer--social-icons-div {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
}

.hub-footer--util-links-div {
  display: flex;
  position: relative;
  margin-left: auto;
  margin-right: auto;

  margin-bottom: 15px;
}

.hub-footer--util-links-div p {
  color: rgb(67, 59, 59);
  font-weight: 400;
  font-size: 0.85em;
  padding: 0px 10px;
  user-select: none;
  cursor: pointer;
}

.hub-footer--copyright-div {
  font-size: 0.75em;
  color: rgb(107, 98, 98);
}

.section-hub--foooter-content {
  width: fit-content;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  margin-top: 30px;
  padding-bottom: 25px;
}
