.logocard--div {
  position: relative;

  grid-row: 5 / span 10;
  grid-column: 3 / span 6;
  width: 100%;
  overflow: hidden;
  border: rgb(216, 209, 209) solid 4px;
  border-radius: 20px;
  height: fit-content;
  /* height: 100%; */
  background-color: white;
  box-shadow: 7px 12px 16px 4px rgba(16, 15, 15, 0.254);
  min-width: 300px;
  /* background-color: blue; */
}

.logocard--img {
  position: relative;
  width: inherit;
  height: 100%;

  /* height: 440px; */
}
